/* stylelint-disable */



@mixin big-desktop {
  @media (max-width: (1500px)) {
    @content;
  }
}

@mixin notebook {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (600px)) {
    @content;
  }
}


@mixin small-mobile {
  @media (max-width: (600px)) {
    @content;
  }
}
